@keyframes shadow-inset-center {
  0% {
    box-shadow: inset 0 0 0 0 transparent;
  }
  to {
    box-shadow: inset 0 0 14px 0 rgb(0 0 0/50%);
  }
}
.div1 {
  align-self: stretch;
  position: relative;
}
.div {
  position: absolute;
  top: 7px;
  left: 11px;
  width: 154px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  padding: 6px 0;
  box-sizing: border-box;
  align-items: center;
  justify-content: flex-start;
}
.div3,
.div5 {
  align-self: stretch;
}
.div5 {
  position: relative;
  color: var(--color-white);
}
.div3 {
  border-radius: var(--br-8xs);
  background-color: rgba(239, 141, 26, 0.46);
  display: flex;
  flex-direction: column;
  padding: 5px 91px 10px 7px;
  align-items: flex-start;
  justify-content: flex-end;
  gap: 5px;
}
.div2,
.div6 {
  position: absolute;
}
.div2 {
  top: 75px;
  left: 10px;
  width: 157px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: left;
  font-size: var(--font-size-mini);
  color: #f8a03a;
}
.div6 {
  width: 100%;
  top: 0;
  right: 0;
  left: 0;
  border-radius: 50%;
  background-color: var(--color-mediumseagreen);
  height: 35px;
}
.phone-icon {
  position: absolute;
  height: 54.48%;
  width: 55.85%;
  top: 25.52%;
  right: 22.08%;
  bottom: 20%;
  left: 22.08%;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
}
.button {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: transparent;
  top: 157px;
  left: 22px;
  width: 35px;
  height: 35px;
}
.button1:hover,
.button2:hover,
.button:hover {
  animation: 1s ease 0s infinite normal none shadow-inset-center;
  opacity: 1;
}
.button,
.child,
.icon {
  position: absolute;
}
.child {
  top: 0;
  left: 0;
  border-radius: 50%;
  background-color: var(--color-lightskyblue);
  width: 35px;
  height: 35px;
}
.icon {
  height: 59.93%;
  width: 53.02%;
  top: 22.89%;
  right: 23.49%;
  bottom: 17.18%;
  left: 23.49%;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
}
.button1,
.item {
  position: absolute;
  width: 35px;
  height: 35px;
}
.button1 {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: transparent;
  top: 157.5px;
  left: 69.33px;
  overflow: hidden;
}
.item {
  top: 0;
  left: 0;
  border-radius: 50%;
  background-color: var(--color-salmon);
}
.button2,
.icon1 {
  position: absolute;
  top: 9px;
  left: 9px;
  width: 17px;
  height: 17px;
  overflow: hidden;
}
.button2 {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: transparent;
  top: 158px;
  left: 118.67px;
  width: 35px;
  height: 35px;
}
.header {
  position: relative;
  background-color: var(--color-black);
  width: 100%;
  height: 215px;
  overflow: hidden;
  text-align: center;
  font-size: 50px;
  color: var(--color-white);
  font-family: var(--font-inter);
}
